<template>
    <div class="pic upload">
        <span>{{label}}</span>
        <div tabindex="0" class="el-upload el-upload--text">
            <label for="upPhoto" class="el-icon-plus avatar-uploader-icon"></label>
            <input @change="fileChange" type="file" name="file" class="el-upload__input" id="upPhoto">
        </div>
        <ul class="list" v-show="imageList.length > 0">
            <li v-for="(item, index) in imageList" :key="index">
                <div>链接地址：<a :href="item">{{item}}</a></div>
                <div @click="del(item)">删除</div>
            </li>
        </ul>
    </div>
</template>

<script>
import { FileChange } from "utils/formData";
export default {
    props:['label','defaultValue'],
    data() {
        return {
            imageList: [],
            // imageList: ['https:www.baidu.com','https:www.baidu.com2'],
        };
    },
    methods: {
        fileChange(e) {
            this.loading = true;
            FileChange(e, res => {
                this.loading = false;
                imageList.unshift(res.url);
            });
        },
        del(item){
            const list = this.imageList.filter(item => {
                return item !== item;
            })
            this.imageList = list;
        },
        getData(){
            let str = this.imageList.join(',');
            return str;
        }
    },
    watch: {
        defaultValue(){
            this.imageList = this.defaultValue.split(',')
        }
    }
};
</script>

<style lang="less" scope>
@import url("./../assets/less/color.less");
@pic-border:#dcdfe6;
.avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 150px;
    height: 150px;
    line-height: 150px;
    border: 1px solid @pic-border;
    border-radius: 6px;
    text-align: center;
}
.pic.upload{
    >span{
        display: inline-block;
        width: 120px;
        text-align: right;
    }
    .avatar-uploader-icon{
        cursor: pointer;
    }
    .list{
        margin: 0;
        padding: 0;
        li{
            list-style: none;
            display: flex;
            margin-left: 110px;
            div{
                margin: 10px 20px 10px 0;
                &:last-child{
                    cursor: pointer;
                }
            }
        }
    }
}
</style>